import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React, { Component } from 'react'

class Header extends Component {
  state = {
    isMenuOpen: false,
  }

  handleMenu = () => {
    this.setState(previousState => ({ isMenuOpen: !previousState.isMenuOpen }))
  }

  closeMenu = () => {
    this.setState({ isMenuOpen: false })
  }

  render = () => {
    const { siteTitle } = this.props
    const { isMenuOpen } = this.state
    const burgerClass = isMenuOpen ? 'is-active' : ''

    return (
      <nav className="navbar" role="navigation" aria-label="main navigation">
        <div className="container">
          <div className="navbar-brand">
            <Link to="/" className="navbar-item is-size-4">
              {siteTitle}
            </Link>

            <button
              type="button"
              className={`navbar-burger ${burgerClass}`}
              aria-label="menu"
              aria-expanded="false"
              onClick={this.handleMenu}
            >
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
            </button>
          </div>
          <div className={`navbar-menu ${burgerClass}`}>
            <div className="navbar-end">
              <Link
                to="/"
                className="navbar-item is-size-5"
                onClick={this.closeMenu}
              >
                Home
              </Link>
              <Link
                to="/projects"
                className="navbar-item is-size-5"
                onClick={this.closeMenu}
              >
                Projects
              </Link>
              <Link
                to="/blog"
                className="navbar-item is-size-5"
                onClick={this.closeMenu}
              >
                Blog
              </Link>
              <Link
                to="/stacks"
                className="navbar-item is-size-5"
                onClick={this.closeMenu}
              >
                Stacks
              </Link>
            </div>
          </div>
        </div>
      </nav>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
